.List,
.listTitle {
    font-size: 1.4rem;
    font-weight: 500;
    color: orange;
}

.main-content {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 1rem;
}

.main-content .content {
    padding-right: 1rem;
    font-size: 1.2rem !important;
    letter-spacing: 0.4px;
    word-spacing: 1px;
    font-family: muli, sans-serif;
    font-weight: 400;
    font-style: normal;
    color: white;
}

@media screen and (max-width:615px) {
    .List li iframe {
        width: 80%;
    }
}

@media screen and (max-width:900px) {
    .main-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        grid-template-columns: none;
        grid-gap: 0rem;
    }

    .main-content .content {
        padding-top: 2rem;
    }
}

.ratting,
.views {
    margin-top: 1rem;
    margin-bottom: 1.5rem;
    font-size: 16px;
    color: white;
    font-family: muli, sans-serif;
}

.ratting {
    color: #4CAF50;
}
.nav-extended{
    background: rgb(27, 40, 56) !important;
    font-family: 'Montserrat', sans-serif !important;
    position: fixed !important;
    top: 0;
}

.brand-logo{
    margin-left: 1.5rem;
}

.sidenav{
    background: rgb(36, 54, 75) !important;
    font-family: 'Montserrat', sans-serif !important;
}

.sidenav li a{
    color: white !important;
}

#anime{
    background: url(/static/media/anime.221863f0.jpg);
}

#games{
    background: url(/static/media/games.3db3948f.jpg);
}

#movies{
    background: url(/static/media/movies.82781757.jpg);
}

#musicVideo{
    background: url(/static/media/musicVideo.3e6eb1e2.jpg);
}

#webSeries{
    background: url(/static/media/webSeries.21136f9e.jpg);
}

#anime,#games,#movies,#musicVideo,#webSeries{
    padding: 140px 30px 0 30px ;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.help,
.feedback {
    display: flex;
    justify-content: center;
    flex-direction: column;
    background: url(/static/media/other.11241277.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding-bottom: 4rem;
}

.help h1,
.feedback h1{
    font-family: 'Montserrat', sans-serif ;
    color: #30afbf;
}

.help p,
.feedback p {
    color: white;
    font-family: muli, sans-serif;
    letter-spacing: 0.4px;
    word-spacing: 1px;
}

.navBarAlt .nav-wrapper{
    background: rgb(36, 54, 75) !important;
    font-family: 'Montserrat', sans-serif !important;
}
.page-footer {
    background-color: #333333;
    color: whitesmoke;
    font-family: muli, sans-serif !important;
}

.social-icon {
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 2rem !important;
}

.fa-whatsapp {
    font-size: 1.2rem !important;
    color: #075e54;
}

.fa-facebook {
    color: #3b5998;
}

.fa-instagram {
    color: #3f729b;
}

.fa-twitter {
    color: #1da1f2;
}

.fa-pinterest {
    color: #E60023;
}

.fa-reddit {
    color: #FF5700;
}

.fa-linkedin {
    color: #0e76a8;
}

.footer-copyright {
    background-color: #383838 !important;
}
